import { createEffect, createEvent, createStore } from 'effector'
// SERVICE
import NewsAndPracticesService from '@services/news-and-practices-service'
// TYPES
import { FetchingNewsAndPracticesPropsT, NewsAndPracticesT } from '@interfaces/news-and-practices'
import { EventStateStoreT } from '@interfaces/common'

/** NEWS **/
export const appendToNews = createEvent<NewsAndPracticesT | NewsAndPracticesT[]>()
export const prependToNews = createEvent<NewsAndPracticesT>()
export const updateNews = createEvent<NewsAndPracticesT>()
export const removeItemFromNews = createEvent<number>()

export const $News = createStore<Array<NewsAndPracticesT>>([])
    .on(appendToNews, (oldState, newState) => {
        if (newState instanceof Array) {
            return [...oldState, ...newState]
        }
        return [...oldState, newState]
    })
    .on(prependToNews, (oldState, newState) => {
        setNewsCount($NewsAndPracticeCount.getState().news + 1)
        return [newState, ...oldState]
    })
    .on(updateNews, (oldState, updatedItem) => oldState.map(item => {
        if (item.id === updatedItem.id) {
            item = updatedItem
        }
        return item
    }))
    .on(removeItemFromNews, (oldState, id) => {
        setNewsCount($NewsAndPracticeCount.getState().news - 1)
        return oldState.filter(item => item.id !== id)
    })

// NEWS STATES
export const setNewsStates = createEvent<EventStateStoreT>()
export const setNewsLoading = createEvent<boolean>()

export const $NewsStates = createStore<EventStateStoreT>({ isFetched: false, isLoading: true, error: false })
    .on(setNewsStates, (_, newState) => newState)
    .on(setNewsLoading, (oldState, newState) => ({ ...oldState, isLoading: newState }))

/** PRACTICES **/
export const appendToPractices = createEvent<NewsAndPracticesT | NewsAndPracticesT[]>()
export const prependToPractices = createEvent<NewsAndPracticesT>()
export const updatePractice = createEvent<NewsAndPracticesT>()
export const removeItemFromPractices = createEvent<number>()

export const $Practices = createStore<Array<NewsAndPracticesT>>([])
    .on(appendToPractices, (oldState, newState) => {
        if (newState instanceof Array) {
            return [...oldState, ...newState]
        }
        return [...oldState, newState]
    })
    .on(prependToPractices, (oldState, newState) => {
        setPracticesCount($NewsAndPracticeCount.getState().practices + 1)
        return [newState, ...oldState]
    })
    .on(updatePractice, (oldState, updatedItem) => oldState.map(item => {
        if (item.id === updatedItem.id) {
            item = updatedItem
        }
        return item
    }))
    .on(removeItemFromPractices, (oldState, id) => {
        setPracticesCount($NewsAndPracticeCount.getState().practices - 1)
        return oldState.filter(item => item.id !== id)
    })

/** NEWS AND PRACTICES COUNT(ALL ITEMS COUNT FOR PAGINATE) **/
const setNewsCount = createEvent<number>()
const setPracticesCount = createEvent<number>()

export const $NewsAndPracticeCount = createStore({ news: 0, practices: 0 })
    .on(setNewsCount, (oldState, newState) => ({ practices: oldState.practices, news: newState }))
    .on(setPracticesCount, (oldState, newState) => ({ news: oldState.news, practices: newState }))

// PRACTICES STATES
export const setPracticesStates = createEvent<EventStateStoreT>()
export const setPracticesLoading = createEvent<boolean>()

export const $PracticesStates = createStore<EventStateStoreT>({ isFetched: false, isLoading: true, error: false })
    .on(setPracticesStates, (_, newState) => newState)
    .on(setPracticesLoading, (oldState, newState) => ({ ...oldState, isLoading: newState }))

// NEWS DATA FOR UPDATING
export const setSelectedNewsData = createEvent<NewsAndPracticesT | null>()

export const $SelectedNewsData = createStore<NewsAndPracticesT | null>(null)
    .on(setSelectedNewsData, (_, newState) => newState)

// NEWS ACTION TYPE
export const setNewsActionType = createEvent<'createNews' | 'createPractice' | 'editNews' | 'editPractice'>()

export const $NewsActionType = createStore<'createNews' | 'createPractice' | 'editNews' | 'editPractice'>('createNews')
    .on(setNewsActionType, (_, newState) => newState)

// REQUESTS
export const fetchNews = createEffect<FetchingNewsAndPracticesPropsT>(({ skip, limit, count, cb }) => {
    NewsAndPracticesService.GetNewsOrPractices({ skip, limit, type: 'NEWS', count }, (err, res) => {
        if (err || !res) {
            setNewsStates({ error: true, isFetched: false, isLoading: false })
            if (cb) {
                cb(err)
            }
            return
        }
        setNewsStates({ error: false, isFetched: true, isLoading: false })
        appendToNews(res.data.rows)
        if (count) {
            setNewsCount(res.data.count as number)
        }
        if (cb) {
            cb(null, res)
        }
    })
})

export const fetchPractices = createEffect<FetchingNewsAndPracticesPropsT>(({ skip, limit, count, cb }) => {
    NewsAndPracticesService.GetNewsOrPractices({ skip, limit, type: 'PRACTICE', count }, (err, res) => {
        if (err || !res) {
            setPracticesStates({ error: true, isFetched: false, isLoading: false })
            if (cb) {
                cb(err)
            }
            return
        }
        setPracticesStates({ error: false, isFetched: true, isLoading: false })
        appendToPractices(res.data.rows)
        if (count) {
            setPracticesCount(res.data.count as number)
        }
        if (cb) {
            cb(null, res)
        }
    })
})