import fetcher from '@http/fetcher'
// TYPES
import { ResCallback } from '@interfaces/common'
import { GetNewsOrPracticesPropsT, GetNewsOrPracticesResT } from '@interfaces/requests/news-and-practices'
import { NewsAndPracticesT } from '@interfaces/news-and-practices'

class NewsAndPracticesService {

    static async GetNewsOrPractices(props: GetNewsOrPracticesPropsT, cb: ResCallback<GetNewsOrPracticesResT>) {
        try {
            const res = await fetcher.get<GetNewsOrPracticesResT>('/news', {
                params: {
                    offset: props.skip,
                    limit: props.limit,
                    count: props.count || undefined,
                    type: props.type
                }
            })

            if (res.status === 200) {
                return cb(null, res)
            }
        } catch (err) {
            return cb(err)
        }
    }

    static async GetNewsOrPracticeById(id: number, type: 'PRACTICE' | 'NEWS', cb: ResCallback<NewsAndPracticesT>) {
        try {
            const res = await fetcher.get<NewsAndPracticesT>(`/news/${id}`, { params: { type } })

            if (res.status === 200) {
                return cb(null, res)
            }
        } catch (err) {
            return cb(err)
        }
    }

    static async CreateNewsAndPractice(data: FormData, type: 'PRACTICE' | 'NEWS', cb: ResCallback<NewsAndPracticesT>) {
        try {
            const res = await fetcher.put<NewsAndPracticesT>(`/news`, data, { params: { type } })

            if (res.status === 201) {
                return cb(null, res)
            }
        } catch (err) {
            return cb(err)
        }
    }

    static async UpdateNewsAndPractice(id: number, data: FormData, type: 'PRACTICE' | 'NEWS', cb: ResCallback<string>) {
        try {
            const res = await fetcher.post<string>(`/news/${id}`, data, { params: { type } })

            if (res.status === 200) {
                return cb(null, res)
            }
        } catch (err) {
            return cb(err)
        }
    }

    static async DeleteNewsOrPracticeItem(id: number, type: 'PRACTICE' | 'NEWS', cb: ResCallback<string>) {
        try {
            const res = await fetcher.delete<string>(`/news/${id}`, { params: { type } })

            if (res.status === 200) {
                return cb(null, res)
            }
        } catch (err) {
            return cb(err)
        }
    }
}

export default NewsAndPracticesService